<template>
    <el-col :span="15">
        <el-form :model="formData" :rules="rules" ref="formData" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="state">
                <el-select v-model="formData.state" placeholder="">
                    <el-option
                        v-for="item in select[0]"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="startTime">
                <el-date-picker
                    v-model="formData.startTime"
                    type="datetime"
                    placeholder=""
                    value-format="yyyy-MM-dd HH:mm:ss">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="endTime">
                <el-date-picker
                    v-model="formData.endTime"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="previewTime">
                <el-date-picker
                    v-model="formData.previewTime"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="rewardTime">
                <el-input-number v-model="formData.rewardTime" :min="0" :max="10000" label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="stageGainMaxValue">
                <el-input-number v-model="formData.stageGainMaxValue" :min="0" :max="10000"
                                 label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="totalRewardValue">
                <el-input-number v-model="formData.totalRewardValue" :min="0" :max="10000"
                                 label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="description">
                <el-input v-model="formData.description" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="rewardCoinId">
                <el-select v-model="formData.rewardCoinId" placeholder="">
                    <el-option
                        v-for="item in tableData"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('formData')">{{addTitle}}</el-button>
                <el-button @click="resetForm('formData')"></el-button>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
    export default {
        name: 'addMiningActivity',
        mounted() {
            this.getRewardType()
            let self = this
            let data = self.$route.query.data;
            if (data) {
                self.addTitle = ''
                self.formData = data;
                self.method = 'PUT'
            } else {
                self.method = 'POST'
            }
        },
        data() {
            return {
                tableData: [],
                method: '',//
                addTitle: '',
                formData: {
                    state: '',
                    description: '',
                    rewardTime: '',
                    stageGainMaxValue: '',
                    startTime: '',
                    endTime: '',
                    previewTime: '',
                    rewardCoinId: '',
                    totalRewardValue: ''
                },
                rules: {
                    state: [{
                        required: true, message: '', trigger: 'change'
                    }],
                    description: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    rewardTime: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    rewardCoinId: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    startTime: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    endTime: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    previewTime: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    stageGainMaxValue: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    totalRewardValue: [{
                        required: true, message: '', trigger: 'change'
                    }],

                },
                select: [
                    [
                        {
                            value: 1,
                            label: ''
                        },
                        {
                            value: 2,
                            label: ''
                        },
                        {
                            value: 3,
                            label: ''
                        }
                    ],
                    []
                ],
            }
        },
        methods: {
            //
            getRewardType() {
                var self = this;
                self.$ajax.get('wcoin/admin/coin/list').then(function (response) {
                    if (response.code === 1) {
                        self.tableData = response.data;
                    }
                })
            },
            submitForm(formName) {
                var self = this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        self.$ajax({
                            url: 'wplatform/admin/mining/activity/',
                            method: self.method,
                            data: self.formData
                        }).then(function (response) {
                            if (response.code === 1) {
                                self.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success',
                                    duration: 1000
                                });
                            } else {
                                self.$notify({
                                    title: '',
                                    message: response.msg,
                                    type: 'error',
                                    duration: 1000
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="stylus">
</style>





