<template>
    <el-col :span="13">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="title">
                <el-input v-model="ruleForm.title"></el-input>
            </el-form-item>
            <el-form-item label="" prop="content">
                <quill-editor
                    ref="myTextEditor"
                    v-model="ruleForm.content"
                    :options="editorOption">
                </quill-editor>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="ruleForm.isShow">
                    <el-radio class="radio" :label=0></el-radio>
                    <el-radio class="radio" :label=1></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">{{addTitle}}</el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
    import "quill/dist/quill.core.css";
    import "quill/dist/quill.snow.css";
    import "quill/dist/quill.bubble.css";
    import {quillEditor, Quill} from "vue-quill-editor";
    import {container, ImageExtend, QuillWatch} from "quill-image-extend-module";

    Quill.register("modules/ImageExtend", ImageExtend);
    export default {
        name: "addNews",
        mounted() {
            let self = this;
            let data = self.$route.query.data;
            if (data) {
                self.addTitle = "";
                self.ruleForm = data;
                self.method = "PUT";
            } else {
                self.method = "POST";
            }
        },
        components: {
            quillEditor
        },
        data() {
            return {
                gameData: [],
                addTitle: "",
                max: 3,
                ruleForm: {
                    title: "",
                    content: "",
                    isShow: 0
                },
                rules: {
                    title: [{
                        required: true, message: "", trigger: "blur"
                    }],
                    content: [{
                        required: true, message: "", trigger: "change"
                    }]
                },
                content: "",
                editorOption: {
                    modules: {
                        ImageExtend: {
                            loading: true, //  
                            name: "uploadOss", // 
                            size: 500, //  M1M = 1024kb
                            action: "http://test.bcrealm.com/api/wuser/oss/uploadFile", // , actionbase64
                            // response 
                            // {code: 200; data:{ url: 'baidu.com'}}
                            //  return res.data.url
                            response: res => {
                                return res.data;
                            },
                            headers: xhr => {
                            }, //  
                            start: () => {
                            }, //  
                            end: () => {
                            }, //  
                            error: () => {
                            }, //  
                            change: (xhr, formData) => {
                            } //  headersformData
                        },
                        toolbar: {
                            container: container, // container
                            handlers: {
                                image: function () {
                                    // 
                                    QuillWatch.emit(this.quill.id);
                                }
                            }
                        }
                    }
                }
            };
        },
        methods: {
            submitForm(formName) {
                var self = this;
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        self
                            .$ajax({
                                url: "wnews/admin/news/",
                                data: self.ruleForm,
                                method: self.method
                            })
                            .then(function (response) {
                                if (response.code === 1) {
                                    self.$notify({
                                        title: "",
                                        message: "",
                                        type: "success",
                                        duration: 1000
                                    });
                                }
                            });
                    } else {
                        console.log("error submit!!");
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    };
</script>

<style lang="stylus">
</style>





