<template>
    <div>
        <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column
                type="selection"
                :span="2">
            </el-table-column>
            <el-table-column
                prop="answerStatus"
                :formatter="formatStatus"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="answerTime"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="startTime"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="endTime"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="previewTime"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="description"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="questionNumber"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="rewardQuantity"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column
                prop="rewardType"
                label=""
                :formatter="formatRewardType"
                :span="4">
            </el-table-column>
            <el-table-column
                prop="failedShareRate"
                label=""
                :span="4">
            </el-table-column>
            <el-table-column label="" :span="4">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">
                    </el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            @current-change="currentChange"
            :current-page.sync="currentPageData"
            background
            layout="prev, pager, next"
            :page-count="totalPages"
            style="margin-top: 20px">
        </el-pagination>
    </div>
</template>

<script>
    // import bus from '@/js/event'

    export default {
        name: 'superActivityList',
        data() {
            return {
                totalPages: 0,
                currentPageData: 1,
                tableData: [
                    {
                        "activityId": 0,
                        "answerStatus": 1,
                        "answerTime": 0,
                        "description": "string",
                        "endTime": "2018-07-30T11:29:19.839Z",
                        "previewTime": "2018-07-30T11:29:19.839Z",
                        "questionNumber": 0,
                        "rewardQuantity": 0,
                        "rewardType": 1,
                        "startTime": "2018-07-30T11:29:19.839Z"
                    },
                    {
                        "activityId": 0,
                        "answerStatus": 2,
                        "answerTime": 0,
                        "description": "string",
                        "endTime": "2018-07-30T11:29:19.839Z",
                        "previewTime": "2018-07-30T11:29:19.839Z",
                        "questionNumber": 0,
                        "rewardQuantity": 0,
                        "rewardType": 2,
                        "startTime": "2018-07-30T11:29:19.839Z"
                    },
                    {
                        "activityId": 0,
                        "answerStatus": 0,
                        "answerTime": 0,
                        "description": "string",
                        "endTime": "2018-07-30T11:29:19.839Z",
                        "previewTime": "2018-07-30T11:29:19.839Z",
                        "questionNumber": 0,
                        "rewardQuantity": 0,
                        "rewardType": 3,
                        "startTime": "2018-07-30T11:29:19.839Z"
                    },
                    {
                        "activityId": 0,
                        "answerStatus": 0,
                        "answerTime": 0,
                        "description": "string",
                        "endTime": "2018-07-30T11:29:19.839Z",
                        "previewTime": "2018-07-30T11:29:19.839Z",
                        "questionNumber": 0,
                        "rewardQuantity": 0,
                        "rewardType": 4,
                        "startTime": "2018-07-30T11:29:19.839Z"
                    }
                ],
                multipleSelection: []
            }
        },
        mounted() {
            this.getData()
        },
        methods: {
            //
            formatStatus(row, column) {
                return row.answerStatus == 1 ? '' : row.answerStatus == 2 ? '' : '';
            },
            // 
            formatRewardType(row, column) {
                let data = ['', '', '', 'DBEX']
                return data[(row.rewardType - 1)]
            },
            getData() {
                var self = this;
                self.$ajax.post('dbex/admin/activity/page?size=20&page=' + self.currentPageData, {}).then(function (response) {
                    if (response.code === 1) {
                        self.tableData = response.data.content
                        self.totalPages = response.data.totalPages
                    }
                })
            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success',
                    duration: 3000
                });
            },
            currentChange() {
                this.getData()
            },
            handleDelete(index, row) {
                var self = this
                self.$ajax.delete('dbex/admin/activity/' + row.activityId).then(function (response) {
                    if (response.code === 1) {
                        self.tableData.splice(index, 1)
                        self.$notify({
                            title: '',
                            message: '',
                            type: 'success',
                            duration: 3000
                        });
                    }
                })
            },
            handleEdit(index, row) {
                this.$router.push({path: '/addSuperActivity', query: {data: row}});
            }
        }
    }
</script>

<style lang="stylus">

</style>





