<template>

    <div>
        <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column
                type="selection"
                :span="2">
            </el-table-column>
            <el-table-column
                prop="freeProbability"
                label=" "
                :span="4">
            </el-table-column>
            <el-table-column
                prop="pointProbability"
                label=" "
                :span="4">
            </el-table-column>
            <el-table-column
                prop="rewardQuantity"
                label=""
                :span="12">
            </el-table-column>
            <el-table-column
                prop="rewardType"
                label=""
                :formatter="formatRewardType"
                :span="2"
                show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                prop="isImportant"
                label=""
                :formatter="isImportant"
                :span="2"
                show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="" :span="4">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">
                    </el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'topicList',
        data() {
            return {
                tableData: [],
                multipleSelection: []
            }
        },
        mounted() {
            this.getData()
        },
        methods: {
            //
            formatRewardType(row, col) {
                let data = ['', '', '', 'DBEX']
                return data[(row.rewardType - 1)]
            },
            isImportant(row, column) {
                return row.isImportant == 1 ? '' : '';
            },
            getData() {
                var self = this;
                self.$ajax.get('dbex/admin/rewardSector/list').then(function (response) {
                    if (response.code === 1) {
                        self.tableData = response.data
                    }
                })
            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success',
                    duration: 1500
                });
            },
            handleDelete(index, row) {
                var self = this
                self.$ajax.delete('dbex/admin/rewardSector/' + row.id).then(function (response) {
                    if (response.code === 1) {
                        self.tableData.splice(index, 1)
                        self.$notify({
                            title: '',
                            message: '',
                            type: 'success',
                            duration: 1500
                        });
                    }
                })
            },
            handleEdit(index, row) {
                this.$router.push({path: '/addReward', query: {data: row}});
            }
        }
    }
</script>

<style lang="stylus">

</style>





